
.title {
  margin: 0;
  padding: .5rem 0;
  text-align: center;
  color: #DDD;
  font-weight: 300;
  background-color: #AAA;
}
.player-status {
  width: 100%;
  background-color: #AAA;
  display: flex;
}
.player-status__item {
    padding: 1rem;
    color: #DDD;
    font-size: 1.1rem;
    font-weight: 400;
}
.player-status__item__events__event {
      position: absolute;
      opacity: 0;
}
.event-enter-active {
  animation: event-in 2s;
}
.event-leave-active {
  animation: event-in 1s reverse;
}
@keyframes event-in {
0% {
    transform: translateY(-10px);
    opacity: 0;
}
50% {
    opacity: 1;
    transform: translateY(0);
}
100% {
    transform: translateY(-5px);
    opacity: 0;
}
}



