
body, html {
  height: 100%;
  margin: 0;
  font-family: sans-serif;
}
.game {
  background-color: #777;
  min-height: 100%;
}
.map-container {
  display: flex;
  justify-content: center;
}
.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal__content {
    background-color: #DDD;
    padding: 1rem 2rem;
    box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.75);
    color: #333;
}
.modal__content .button {
      float: right;
}
.button {
  background-color: transparent;
  border: 2px solid #333;
  color: #333;
  margin: .25rem 0;
  font-size: 1.05rem;
  transition: all .3s ease;
  cursor: pointer;
}
.button:focus {
    outline: none;
}
.button:hover {
    background-color: #333;
    color: #DDD;
}



